.section{
    display: flex;
    margin-top: 5%;
    flex-wrap: wrap;
    justify-content: space-around;
}

.card{
    width: 20%;
}
@media only screen and (max-width: 600px) {
    .section{
        flex-direction: column;
    }
    .card{
        width: 80%;
        margin-left: 10%;
    }
  }
